/*
Помощь по синтаксису
.selector{  --------   Вложенность селекторов
	.children_selector{
	}
}

.selector{  --------   Подстановка дочернего элемента без пробела(результат .selector:hover)
	&hover{
	}
}

@break_md: 780px; ------ создание переменной

Переменная видна в том селекторе, где она создана

@import "reset.less"; ------ импорт дополнительного файла less

Миксин - функция.
.row-flex(){  ----------- миксин, применяет к выбранному селектору
	display:flex
}
.md(@name, @value){ -------  миксин с переменными(вызов md(1,2))
	
}

--------------------------SMARTGRID---------------------
.reset() ---  сброс стилей
.wrapper() --- элемент, центрующий сайт. 
	.wrapper{ --- создание класса, который применяет wrapper
		.wrapper();
	}

.row-flex() --- дается контейнеру, где находится все элементы
.col() - стратегия поведения, 
.size() -- размер колонок, сколько колонок займет объекс

*/

