.menu-julia-container{
	
	a{
		text-decoration: none;
		color: black;
		
	}
	visibility: visible;
	.menu{
		
		width: 900px;
		z-index: 5000;
		.md-block({
			width: 700px;
		});
		.sm-block({
			width: 100%;
			display: block;
			padding-top: 70px;
			background-color: rgba(0,0,0,0.9);
			top: 0;
			position: absolute;
			visibility: hidden;
		});
		display: flex;
		flex-wrap: wrap;
		justify-content: center; 
		margin: 0 auto;
		visibility: visible;
		li a:hover{
			padding-bottom: 2px;
			border-bottom: 1px solid #9d81ba;
			.sm-block({
				padding-bottom: 10px;
				border-bottom: 0px;
			});
		}
		.menu-item{
			.sm-block({
				text-transform: uppercase;
				font-size: 14pt;
				//background-color: rgba(0,0,0,0.46);
			});
			position: relative;
			
			&:hover >.sub-menu{
				visibility: visible;
				transition: opacity 3s;
			}
			
			flex-grow: 1;

			text-align: center;
			
			a{ 
				
				.sm-block({
				display: block;
					padding-top: 10px;
				padding-bottom: 10px;
					color: white;
					
				});
			}
			.sub-menu{
				.sm-block({
					position: static;
					display: flex;
					flex-direction: column;
					justify-content: center;
					width: 100%;
					background-color: #454545;
					
					
				});
				position: absolute;
				left: calc(50% - 100px);
				visibility: hidden;
				//visibility: visible;
				display: block;
				width: 200px;
				padding-top: 12px;
				z-index: 10;
				.menu-item{
					
					a{
						.sm-block({
							background-color:rgba(0,0,0,0.00);
							font-size: 12pt;
							color: white;
						});
						display: block;
						padding-top: 10px;
						padding-bottom: 10px;
						width: 100%;
						margin: 3 0;
						background-color: rgba(221,221,221,0.74);
						color: black;
						&:hover{
							padding-bottom: 10px;
							border-bottom: 0px;
							color: white;
							background-color: rgba(147,132,161,0.9);
							.sm-block({
								background-color: rgba(0,0,0,0.00);
								color:aqua;
							});
						}
					}
				}
			}
		}
	}
}

